<template>
  <div>
    <category-nav></category-nav>
    <section class="category-header">
      <h1>{{ $store.state.selectedCategoryName }} Books</h1>
    </section>
    <category-book-list></category-book-list>
  </div>
</template>

<script>
import CategoryNav from "@/components/CategoryNav";
import CategoryBookList from "@/components/CategoryBookList";

export default {
  name: "category",
  components: {
    CategoryNav,
    CategoryBookList,
  },
  created: function () {
    this.$store.dispatch("selectCategory", this.$route.params.name);
    this.$store.dispatch("fetchSelectedCategoryBooks");
  },
  watch: {
    $route: {
      immediate: true,
      handler(to) {
        document.title = to.meta.title || "Categories";
      },
    },
  },
};
</script>

<style scoped>
.category-header {
  padding: 8px;
  padding-top: 30px;
  padding-left: 170px;
  color: #800000;
}
</style>
